import React from 'react';
import { Row, Col, Card, Table } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getGiftCards, deleteGiftCards, getReedemHistory } from '../../api/payment/giftCards';
import GiftCardsFilter from '../../App/components/SearchFilters/GiftCardsFilter';
import { getGiftCardsSearchFilterObject, getGiftCardReedemsSearchFilterObject } from '../../helpers/getSearchFilterObject';
import { getStartEnd } from '../../helpers/getStartEnd';
import GiftCardReedemsFilter from '../../App/components/SearchFilters/GiftCardReedemsFilter';

const GiftCards = (props) => {
    const [reedemHistoryArr, setReedemHistoryArr] = React.useState([]);
    const [loading, setLoading] = React.useState(true)
    const [deleteData, setDeleteData] = React.useState({ showModal: false, giftCardUuid: null })
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();

        const requestJson = {
            "action": "get",
            "filter": "none",
            ...getGiftCardReedemsSearchFilterObject(props),
            ...getStartEnd(props)
        };


        console.log(JSON.stringify(requestJson));
        getReedemHistory(requestJson, controller.signal)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setReedemHistoryArr(res.json.redeeme_history);
                    setInstanceCount(res.json.count)
                    setLoading(false);
                }
            })
            .catch((err) => {
                console.log(err)
            });
        return () => controller.abort()
    }, []);






    const ReedemHistoryList = reedemHistoryArr.map((instance, index) => {
        return (
            <tr key={index}>
                <th scope="row">{instance.id}</th>
                <td>{instance.date}</td>

                <td
                    onClick={() => {
                        props.history.push('/customer/' + instance.customer)
                    }}
                    style={{ cursor: 'pointer', color: '#55588b' }}>{instance.customer_str}</td>
                <td>{instance.invoice}</td>
                <td>{instance.gift_card.name}</td>
                <td>
                    {instance.gift_card.discount_type === 'fixed' ?
                        `Rs. ${instance.gift_card.rate}` :
                        `${instance.gift_card.rate}%`}
                </td>
                <td>{instance.unique_card.code}</td>
            </tr>
        )
    })

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="p-b-10">Gift Card Reedem History</h4>
                <div>
                </div>
            </Row>
            <Row>
                <Col>

                    <Card>
                        <Card.Body>
                            <GiftCardReedemsFilter />
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Date</th>
                                        <th>Customer</th>
                                        <th>Invoice</th>
                                        <th>Card Name</th>
                                        <th>Value</th>
                                        <th>Unique Card</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {ReedemHistoryList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(reedemHistoryArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>

                    </Card >




                </Col>
            </Row>
        </Aux>
    );
}

export default GiftCards;